  --color-popover: #ffffff;
  --color-popover-foreground: #0a0a0a;
  --color-primary: #9D17A0;
  --color-primary-foreground: #ffffff;
  --color-secondary: #f5f5f5;
  --color-secondary-foreground: #0a0a0a;
  --color-muted: #f5f5f5;
  --color-muted-foreground: #737373;
  --color-accent: #f5f5f5;
  --color-accent-foreground: #0a0a0a;
  --color-destructive: #ef4444;
  --color-destructive-foreground: #ffffff;
  --color-border: #e5e5e5;
  --color-input: #e5e5e5;
  --color-ring: #9D17A0;
  --font-size-h1: clamp(1.75rem, 5vw, 3rem);
  --font-size-h2: clamp(1.5rem, 4vw, 2.25rem);
  --font-size-h3: clamp(1.25rem, 3vw, 1.875rem);
  --font-size-body: clamp(0.875rem, 2vw, 1rem);
  --font-size-small: clamp(0.75rem, 1.5vw, 0.875rem);
  --spacing-section: clamp(2rem, 5vw, 4rem);
  --spacing-component: clamp(1rem, 3vw, 2rem);
  --spacing-element: clamp(0.5rem, 2vw, 1rem);
}
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-size-adjust: 100%;
}
body {
  background: var(--color-background);
  color: var(--color-foreground);
  font-feature-settings: "rlig" 1, "calt" 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}
@keyframes grid-flicker {
  0%,
  100% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.7;
  }
}
.card-hover {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.card-hover:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.button-scale {
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.button-scale:hover {
  transform: scale(1.05);
}
.button-scale:active {
  transform: scale(0.98);
}
::-webkit-scrollbar {
  width: 10px;
}
@media (max-width: 768px) {
  ::-webkit-scrollbar {
    width: 6px;
  }
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #9D17A0;
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: #7D1280;
}
@keyframes fade-in-page {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.page-fade {
  animation: fade-in-page 0.3s ease-out;
}
.glow-on-hover {
  transition: box-shadow 0.3s ease-in-out;
}
.glow-on-hover:hover {
  box-shadow: 0 0 20px rgba(157, 23, 160, 0.3);
}
.logo-container {
  display: flex;
  align-items: center;
  height: 60px;
}
.logo-image {
  width: auto;
  height: 100%;
  max-width: 200px;
  max-height: 60px;
}
@media (max-width: 768px) {
  .logo-container {
    height: 50px;
  }
  .logo-image {
    max-width: 160px;
    max-height: 50px;
  }
}
@media (max-width: 480px) {
  .logo-container {
    height: 45px;
  }
  .logo-image {
    max-width: 140px;
    max-height: 45px;
  }
}
@keyframes popup {
  0% {
    opacity: 0;
    transform: scale(0.8) translateY(10px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
@keyframes bot {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}
@keyframes slide-up {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-popup {
  animation: popup 0.5s ease-out;
}
.animate-bot {
  animation: bot 2s ease-in-out infinite;
}
.animate-slide-up {
  animation: slide-up 0.3s ease-out;
}
@keyframes fade-in {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.animate-fade-in {
  animation: fade-in 0.3s ease-out;
}
.touch-target {
  min-width: 44px;
  min-height: 44px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.touch-target-sm {
  min-width: 36px;
  min-height: 36px;
}
.mobile-only {
  display: block;
}
@media (min-width: 768px) {
  .mobile-only {
    display: none;
  }
}
.desktop-only {
  display: none;
}
@media (min-width: 1024px) {
  .desktop-only {
    display: block;
  }
}
input[type="text"],
input[type="email"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="url"],
textarea {
  font-size: 16px;
}
@media (min-width: 768px) {
  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="url"],
  textarea {
    font-size: 14px;
  }
}
.grid-responsive-cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
@media (min-width: 430px) {
  .grid-responsive-cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .grid-responsive-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
  }
}
@media (min-width: 1440px) {
  .grid-responsive-cards {
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
